import { Component, OnInit } from '@angular/core';
import { DishesService } from 'src/app/service/dishes.service';
import { Dish } from 'src/app/model/dish.model';
import { AuthenticationService } from 'src/app/service/security/authentication-service.service';
import { DishCategory } from 'src/app/model/dishCategory.model';
import { CategoryService } from 'src/app/service/category.service';

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.css']
})
export class MainPageComponent implements OnInit {

  dishes: Dish[] = [];
  page = 0;
  category = '';
  categories: DishCategory[] = [];
  dish: Dish = {
    name: '',
    price: 0,
    category: {
      name: ''
    }
  };

  constructor(private dishService: DishesService,
              private categoryService: CategoryService,
              private authehticationService: AuthenticationService) { }

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    this.dishService.getAll(this.page, this.category).subscribe(data => { this.dishes = data; } );
    this.categoryService.getAll().subscribe(data => { this.categories = data; });
  }

  nextPage() {
    this.page += 1;
    this.loadData();
  }

  prevPage() {
    if (this.page >= 0) {
      this.page -= 1;
      this.loadData();
    }
  }

  search() {
    this.loadData();
    this.category = '';
  }

  isLoggedIn() {
    return this.authehticationService.isLoggedIn();
  }

  save() {
    // Ovo je dummy save. Implementirati pravi save
    // Uvesti Bootstrap temu
    // Pocistiti projekat od koda iz starter projekta
    console.log(this.dish);
  }
}
