import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Student, StudentInterface } from '../../model/student.model';
import { Order } from '../../model/order.model';
import { AuthenticationService } from '../../service/security/authentication-service.service';
import { StudentsService } from 'src/app/service/students.service';

@Component({
  selector: 'app-students-page',
  templateUrl: './students-page.component.html',
  styleUrls: ['./students-page.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class StudentsPageComponent implements OnInit {

  public students: StudentInterface[];

  public activeStudent: StudentInterface;

  public isAdministrator: boolean;

  constructor(
    private authenticationService: AuthenticationService,
    private studentService: StudentsService) { }

  ngOnInit() {
    this.isAdministrator = this.authenticationService.getCurrentUser().roles.indexOf('ROLE_ADMIN') !== -1;

    this.resetActiveStudent();
    this.loadData();
  }

  loadData() {
    this.studentService.getStudents().subscribe(
      (res: any) => {
        this.students = res;
      }
    );
  }

  addStudent(student: Student) {
    this.studentService.postStudent(student).subscribe(res => this.loadData())
  }


  deleteStudent(id: number) {
    this.studentService.deleteStudent(id).subscribe(res => this.loadData());
  }

  editStudent(student: Student) {
    this.studentService.putStudent(student).subscribe(res => {
      this.resetActiveStudent();
      this.loadData();
    })
  }

  setActiveStudent(student: Student) {
    this.activeStudent = new Student(student);
  }

  search(cardNumber: string) {
    this.studentService.searchStudents(cardNumber).subscribe((students: Student[]) => this.students = students);
  }

  resetActiveStudent() {
    this.activeStudent = new Student({
      cardNumber: '',
      firstName: '',
      lastName: '',
    })
  }

  filterByAvgGrade(grade: number) {
    this.studentService.getWithGradeGreaterThan(grade).subscribe((students: Student[]) => this.students = students)
  }

  filterByMaxGrade() {
    this.studentService.getMaxAverage().subscribe((students: Student[]) => this.students = students);
  }

  filterByNextYearCondition() {
    this.studentService.getWithNextYearCondition().subscribe((students: Student[]) => this.students = students);
  }

  sortByGrade(order: Order) {
    this.studentService.getSorted(order).subscribe((students: Student[]) => this.students = students);

  }

}
